.sci{
    position: relative;
    display: flex;
    list-style: none;
    
    color:cadetblue;
}
.sci li::before{
    content: attr(data-text);
    position: absolute;
    top: 50%;
    left:50%;
    font-size: 14vw;
    pointer-events: none;
    -webkit-transform: translate(-50%,calc(-50% + 120px));
            transform: translate(-50%,calc(-50% + 120px));
    text-align: center;
    opacity: 0;
    font-weight: 700;
    transition: 0.5s;
    

}
.sci li:hover::before{
    opacity: 0.2;
    color: black;
    -webkit-transform: translate(-50%,calc(-50% + 150px));
            transform: translate(-50%,calc(-50% + 150px));
}
.sci li a{
    position: relative;
    display: inline-block;
    width: 130px;
    
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:#fff;
    color: #333;
    border-radius: 10px;
    text-decoration: none;
    margin: 40px;
    font-size: 4em;
    box-shadow: 0 25px 35px rgba(0, 0, 0, 0.1);
    transition: background 0.50s;
}
.linkedin:hover{
    background: linear-gradient(100deg,transparent,#0357f4);
    -webkit-animation: move 3s ease-out infinite;
            animation: move 3s ease-out infinite;
}
@-webkit-keyframes move{
    0%{
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
        -webkit-transform: rotateY(50deg);
                transform: rotateY(50deg);
    }
    
    
    100%{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
        -webkit-transform: rotateY(-50deg);
                transform: rotateY(-50deg);
    }
}
@keyframes move{
    0%{
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
        -webkit-transform: rotateY(50deg);
                transform: rotateY(50deg);
    }
    
    
    100%{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
        -webkit-transform: rotateY(-50deg);
                transform: rotateY(-50deg);
    }
}
